<template>
  <v-container fluid grid-list-md v-if="selectedElement" pa-0>
    <v-text-field solo flat
      v-model="propertySearch"
      prepend-icon="search"
      append-icon="close"
      label="Search Property">
    </v-text-field>
    <v-expansion-panel focusable>
      <v-expansion-panel-content class="full-size"
        v-for="property in properties" :key="property.title">
        <div slot="header">{{ property.title }}</div>
        <v-layout>
      <v-flex md5>
        <v-select solo flat
          placeholder="Bind"
          v-model="property.bind"
          :items="states" item-text="name">
        </v-select>
      </v-flex>
      <v-flex md7>
        <v-text-field solo flat v-if="property.type == types[0]"
          placeholder="Value"
          v-model="property.value"
          v-on:input="setValue(selectedElement, property.title, $event)">
        </v-text-field>
        <div v-if="property.type == types[3]">
          <v-select v-model="property.valueType" solo flat :items="['Adjust', 'Fixed', 'Percentage']"
            @change="setValue(selectedElement, property.title, $event == 'Adjust' ? 'auto' : undefined)">
          </v-select>
          <v-text-field type="number" solo flat min="0"
            placeholder="Value"
            v-if="property && property.valueType == 'Fixed'"
            v-on:input="setValue(selectedElement, property.title, $event + 'px')">
          </v-text-field>
          <v-text-field type="number" solo flat min="0" suffix="%"
            placeholder="Value"
            v-if="property && property.valueType == 'Percentage'"
            v-on:input="setValue(selectedElement, property.title, $event + '%')">
          </v-text-field>
        </div>
        <div v-if="property.type == types[2]">
          <v-checkbox type="checkbox"
            :label="property.value ? 'True' : 'False'"
            v-model="property.value">
          </v-checkbox>
        </div>
      </v-flex>
      </v-layout>
      </v-expansion-panel-content>
    </v-expansion-panel>
  </v-container>
</template>

<script>
import {
  ElementModel,
  PropertyModel,
  StateModel,
  Types
} from "./ElementModel.vue";
import { Container, Draggable } from "vue-smooth-dnd";
import { getValue, setValue } from "./store";

export default {
  name: "PropertiesTab",
  data() {
    return {
      propertySearch: '',
      types: Types
    };
  },
  computed: {
    properties() {
      var selectedElement = this.$store.state.selectedElement;
      if(!selectedElement) return undefined;

      return selectedElement.properties
        .filter(x=> x.title.toLowerCase().startsWith(this.propertySearch.toLowerCase()))
    },
    selectedElement() {
      return this.$store.state.selectedElement;
    },
    states() {
      return this.$store.state.states;
    }
  },
  methods: {
    getValue,
    setValue
  },
  components: {
    Draggable,
    Container
  }
};
</script>

<style scoped>
.components-container {
  width: 100%;
}

.my-drag {
  transition: all .18s ease;
  opacity: 0.8;
  transform: rotateZ(1deg);
  font-family: 'Roboto';
}

.my-drop {
  opacity: 1;
  transform: rotateZ(0deg);
  font-family: 'Roboto';
}
</style>



